.item {
  width: 18%;
  font-family: "Roboto", "Helvetica", "Arial", "sans-serif";
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.43;
  letter-spacing: 0.01071em;
  padding: 4px;
  text-align: center;
  color: black;
}

.smallItem {
  composes: item;
  width: 5%;
}


.search {
  position: relative;
  border-radius: 4;
  background-color: alpha(white, 0.15);
  margin-right: 8px;
  margin-left: 0;
  width: auto;
  /* [theme.breakpoints.up("sm")]: {
    width: "auto",
  }, */
}

.search:hover {
  background-color: alpha(white, 0.25);
}

.stack {
  flex-direction: column;
}

.searchIconWrapper {
  height: 100%;
  position: absolute;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 1200px) {
  .item {
    width: 100%;
  }
}
